import "./Step1.scss";
import { Button } from "../../components/Button/Button";
import { Input } from "../../components/Input/Input";
import { Select } from "../../components/Select/Select";
import { Sex } from "../../utils/enums";
import { Link, useNavigate } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import stepper_1 from "../../images/stepper_1.svg";

export default function Step1() {
  const nicknameRegExp = /^[A-z-0-9]*$/;
  const nameSurnameRegExp = /^[A-z-]+$/;

  const navigateNextPage = useNavigate();

  return (
    <div className="step1__container">
      <img src={stepper_1} alt="step_1" />
      <Formik
        initialValues={{
          nickname: "",
          name: "",
          surname: "",
          sex: "",
        }}
        validationSchema={Yup.object({
          nickname: Yup.string()
            .max(30, "  30 ")
            .matches(nicknameRegExp, "     ")
            .required("    "),
          name: Yup.string()
            .max(50, "  50 ")
            .matches(nameSurnameRegExp, "   ")
            .required("    "),
          surname: Yup.string()
            .max(50, "  50 ")
            .matches(nameSurnameRegExp, "   ")
            .required("    "),
          sex: Yup.string().required("    "),
        })}
        onSubmit={async (values, { setSubmitting }) => {
          await new Promise((r) => setTimeout(r, 500));
          setSubmitting(false);
          console.log(JSON.stringify(values, null, 2));
          navigateNextPage("/step2");
        }}
      >
        <Form>
          <div className="user-input">
            <Input
              label="Nickname"
              id="field-nickname"
              name="nickname"
              type="text"
              placeholder=" nickname "
            />
          </div>
          <div className="user-input">
            <Input
              label=""
              id="field-name"
              name="name"
              type="text"
              placeholder=" "
            />
          </div>
          <div className="user-input">
            <Input
              label=""
              id="field-surname"
              name="surname"
              type="text"
              placeholder=" "
            />
          </div>
          <div className="user-input">
            <Select label="" id="field-sex" name="sex">
              <option value=""> </option>
              <option value={Sex.MAN} id="field-sex-option-man">
                
              </option>
              <option value={Sex.WOMAN} id="field-sex-option-woman">
                
              </option>
            </Select>
          </div>
          <div className="step1__container_buttons">
            <Link to="/">
              <Button type="submit" id="button-back">
                
              </Button>
            </Link>
            <Button type="submit" id="button-next">
              
            </Button>
          </div>
        </Form>
      </Formik>
    </div>
  );
}
